import React from 'react';
import BookCard from './BookCard'; //  BookCard
import './MyBooks.css';

function MyBooks({ myBooks, onRemoveBook }) {
    return (
        <div className="mybook-container">
            {myBooks.length === 0 ? (
                <p>     .</p>
            ) : (

                myBooks.map((book) => (
                    <BookCard
                        key={book.id}
                        book={book}
                        onRemoveBook={() => onRemoveBook(book.id)} //    
                    />
                ))
            )}
        </div>
    );
}

export default MyBooks;
