import React, { useState, useEffect } from 'react';
import axios from 'axios';
import LoginModal from './LoginModal';
import './LoginModal.css'; //  

const Login = ({ isOpen, onClose, onRegisterClick, onLoginSuccess }) => {
    const [credentials, setCredentials] = useState({ username: '', password: '' });
    const [error, setError] = useState('');

    useEffect(() => {
        if (isOpen) {
            setError(''); //      
        }
    }, [isOpen]);

    const handleChange = (e) => {
        setCredentials({ ...credentials, [e.target.name]: e.target.value });
    };

    const handleSubmit = (e) => {
        e.preventDefault(); //    

        if (!credentials.username || !credentials.password) {
            setError(",   .");
            return;
        }

        console.log("   :", credentials);
        axios.post('http://localhost:5000/api/login', credentials)
            .then(response => {
                console.log(" ", response.data);
                localStorage.setItem('token', response.data.token);
                onLoginSuccess({ username: credentials.username }); //      
                onClose(); //   
            })
            .catch(error => {
                console.error(" :", error);
                setError("    .");
            });
    };

    if (!isOpen) return null; //  ,     

    return (
        <div className="author-modal-overlay">
            <div className="author-modal">
                <span className="close-auth-modal" onClick={onClose}>&times;</span>

                <LoginModal
                    username={credentials.username}
                    password={credentials.password}
                    error={error}
                    onChange={handleChange}
                    onRegisterClick={onRegisterClick}
                    isOpen={isOpen}
                    onClose={onClose}
                    onSub={handleSubmit} //  
                />
            </div>
        </div>
    );
};

export default Login;
