import './App.css';
import React, { useState, useEffect, useCallback } from 'react';
import { Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import Login from './components/Login';
import Register  from "./components/Register";
import Header from './components/Header';
import BookList from './components/BookList';
import MyBooks from './components/MyBooks';
import axios from 'axios';
import { motion, AnimatePresence } from 'framer-motion';

function App() {
  const [isLoginModalOpen, setLoginModalOpen] = useState(false);
  const [isRegisterModalOpen, setRegisterModalOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [username, setUsername] = useState(null);
  const [books, setBooks] = useState([]);
  const [myBooks, setMyBooks] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [filteredBooks, setFilteredBooks] = useState([]);
  const navigate = useNavigate();
  const location = useLocation();

  const pageTransition = {
    type: "tween",
    ease: "anticipate",
    duration: 0.5
  };

  //  pageTransition  
  const pageVariants = {
    initial: {
      opacity: 0,          //  
      x: 100,              //   
      y: 50,               //    
      scale: 0.8,          //  
    },
    in: {
      opacity: 1,          //  
      x: 0,                //  
      y: 0,                //  
      scale: 1,            //  
      transition: {
        duration: 0.1,    //  
        ease: "easeOut",  //   
      }
    },
    out: {
      opacity: 0,          //   
      x: -100,             //  
      y: -50,              //  
      scale: 0.8,          // 
      transition: {
        duration: 0.1,    //  
        ease: "easeIn",   //   
      }
    },
  };





  const storageKey = username ? `myBooks_${username}` : 'myBooks_default'; //    LocalStorage

  useEffect(() => {
    const token = localStorage.getItem('token');
    const storedUsername = localStorage.getItem('username');

    if (token) {
      setIsAuthenticated(true); //  ,  
    } else {
      setIsAuthenticated(false); //   ,   
    }

    if (storedUsername) {
      setUsername(storedUsername);
    }
  }, []);

  const handleLogout = () => {
    setUsername(null); //  
    localStorage.removeItem('token'); //  
    localStorage.removeItem('username'); //   
    setIsAuthenticated(false);
    navigate('/'); //    
  };


  useEffect(() => {
    const storedUsername = localStorage.getItem('username');
    if (storedUsername) {
      setUsername(storedUsername);
      setIsAuthenticated(true);
    }
  }, []);

  useEffect(() => {
    const storedBooks = localStorage.getItem(storageKey);
    if (storedBooks) {
      setMyBooks(JSON.parse(storedBooks));
    }
  }, [storageKey]); //  storageKey  

  useEffect(() => {
    localStorage.setItem(storageKey, JSON.stringify(myBooks));
  }, [myBooks, storageKey]); //  storageKey  


  useEffect(() => {
    const fetchBooks = async () => {
      try {
        const response = await axios.get('/api/books');
        setBooks(response.data);
        setFilteredBooks(response.data);
      } catch (error) {
        console.error('   :', error);
      }
    };

    fetchBooks();
  }, []);

  const handleSearch = useCallback(() => {
    if (!searchQuery) {
      setFilteredBooks(books); //      
    } else {
      const lowerCaseQuery = searchQuery.toLowerCase();
      const filtered = books.filter(book =>
        book.title.toLowerCase().includes(lowerCaseQuery) ||
        book.author.toLowerCase().includes(lowerCaseQuery)
      );
      setFilteredBooks(filtered);
    }
  }, [searchQuery, books]); //  searchQuery  books  

  useEffect(() => {
    handleSearch(); //     searchQuery
  }, [searchQuery, handleSearch]);

  const addBookToMyBooks = (book) => {
    if (!myBooks.some(existingBook => existingBook.id === book.id && existingBook.title === book.title && existingBook.author === book.author)) {
      setMyBooks(prevBooks => [...prevBooks, book]);
      alert(`${book.title}    `); //  
    } else {
      alert(`${book.title}     `); //   
    }
  };

  const removeBookFromMyBooks = (bookId) => {
    setMyBooks(myBooks.filter((book) => book.id !== bookId));
  };

  const handleRegisterSuccess = () => {
    setRegisterModalOpen(false); //    
    setLoginModalOpen(true); //    
  };

  const handleLoginSuccess = (user) => {
    console.log(' ', user);
    setUsername(user.username); //    
    setIsAuthenticated(true); //    true
    localStorage.setItem('username', user.username);
    setLoginModalOpen(false); //    
  };

  return (
      <div className="App">
        <Header 
          onLoginClick={() => setLoginModalOpen(true)}
          username={username} 
          onLogout={handleLogout}
          searchQuery={searchQuery}       
          setSearchQuery={setSearchQuery}
          onSearch={handleSearch}
        />
        <Login
          isOpen={isLoginModalOpen}
          onClose={() => setLoginModalOpen(false)}
          onRegisterClick={() => {
            console.log("   "); //  
            setLoginModalOpen(false); //    
            console.log("   "); //   
            setRegisterModalOpen(true); //    
            console.log("   "); //   
          }}
          onLoginSuccess={handleLoginSuccess}
        />

        <Register
          isOpen={isRegisterModalOpen} //    
          onClose={() => setRegisterModalOpen(false)} //   
          onRegisterSuccess={handleRegisterSuccess} //    
        />

      <AnimatePresence mode="wait">
        <Routes location={location} key={location.pathname}>
          <Route
            path="/"
            element={
              <motion.div
                initial="initial"
                animate="in"
                exit="out"
                variants={pageVariants}
                transition={pageTransition}
              >
                <BookList books={filteredBooks} isAuthenticated={isAuthenticated} addBookToMyBooks={addBookToMyBooks} />
              </motion.div>
            }
          />
          <Route
            path="/my-books"
            element={
              <motion.div
                initial="initial"
                animate="in"
                exit="out"
                variants={pageVariants}
                transition={pageTransition}
              >
                <MyBooks myBooks={myBooks} onRemoveBook={removeBookFromMyBooks} />
              </motion.div>
            }
          />
        </Routes>
      </AnimatePresence>
    </div>
  );
}

export default App;