import React from 'react';
import './LoginModal.css'; //  

const LoginModal = ({ username, password, error, onChange, onRegisterClick, isOpen, onClose, onSub }) => {
    if (!isOpen) return null; //  isOpen ,   

    return (
            <div className="author-modal">
                <span className="close-auth-modal" onClick={onClose}>&times;</span>
                <h2></h2>
                {error && <div className="error">{error}</div>}

                <form onSubmit={onSub}> {/*  onSubmit,   Login */}
                    <input
                        type="text"
                        name="username"
                        value={username}
                        onChange={onChange}
                        placeholder=" "
                        required
                    />
                    <input
                        type="password"
                        name="password"
                        value={password}
                        onChange={onChange}
                        placeholder=""
                        required
                    />
                    <button type="submit" className="login__btn"></button> {/*     */}
                    <button type="button" className="auth__regestr-btn" onClick={onRegisterClick}>
                        
                    </button>
                </form>
            </div>
    );
};

export default LoginModal;
